import React, { Component } from 'react'
import { Tabs, NavBar, Icon, WingBlank } from 'antd-mobile';
import { getOrder } from '../api/index'
import '../style/orderlist.css'
export class OrderList extends Component {
    constructor(props) {
        super(props)

        this.state = {
            count: 0,
            orders: []
        }
    }
    UNSAFE_componentWillMount() {
        // renderid idNumberNaN
        let id = Number(this.props.location.pathname.split('/').pop())
        if (id) {
            this.setState({ id })
        }
        // 
        getOrder().then(res => {
            const { meta: { status }, message: { count, orders } } = res.data
            if (status === 200) {
                this.setState({
                    count,
                    orders
                })
            }
        })
    }
    componentWillUpdate() {
        const that = this;
        window.addEventListener("popstate",function(e) {
            that.props.history.push('/my')
            },false);
    }
    // 2019-9-12 22:36:35
    convertTime = (create_time) => {
        let time = new Date(parseInt(create_time) * 1000)
        let y = time.getFullYear(); //getFullYear
        let M = time.getMonth() + 1; // getMonth Date  (0 ~ 11)
        let d = time.getDate(); // getDate Date  (1 ~ 31)
        let h = time.getHours(); // getHours Date  (0 ~ 23)
        let m = time.getMinutes(); // getMinutes Date  (0 ~ 59)
        let s = time.getSeconds(); // getSeconds Date  (0 ~ 59)
        return y + '-' + M + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
    render() {
        const tabs = [
            { title: '' },
            { title: '' },
            { title: '' },
        ];
        return (
            <div>
                <NavBar
                    mode="dark"
                    leftContent={<Icon type='left' />}
                    onLeftClick={() => this.props.history.push('/my')}
                    className="nav-bar-style"
                >
                    {this.state.count ? `(${this.state.count})` : ''}
                </NavBar>
                <Tabs tabs={tabs} initialPage={this.state.id ? this.state.id : 0} animated={false} useOnPan={false}>
                    <WingBlank style={{ marginTop: 20, marginBottom: 60 }}>
                        <div>
                            {this.state.orders.length ?
                                this.state.orders.map(v => (
                                    v.goods.length ?
                                        <div key={v.order_id} className="single-order-list">
                                            {v.goods.map(v1 => (
                                                <div key={v1.goods_id} className="single-order">
                                                    <img src={v1.goods_small_logo} alt="" />
                                                    <div className="order-content">
                                                        <div className="order-title ellipsis-2">
                                                            {v1.goods_name}
                                                        </div>
                                                        <div className="num-price">
                                                            <span>{v1.goods_price}</span> X <span>{v1.goods_number}</span>
                                                        </div>
                                                        <div className="order-price">
                                                            <span>{v1.goods_number}</span>
                                                            <span></span>
                                                            <span>&yen;{v1.goods_total_price}.00</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            ))}
                                            <div className="order-infos">
                                                <h4 className="title"></h4>
                                                <div className="order-infos-content">
                                                    <div>
                                                        <span></span>
                                                        <span>{v.order_number}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{this.convertTime(v.create_time)}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{v.consignee_addr}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{v.order_fapiao_title}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{v.total_count}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span className="total-price">{v.total_price}.00</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span></span>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>
                                        : ''
                                ))
                                : ''}
                        </div>
                    </WingBlank>
                    <WingBlank style={{ marginTop: 20, marginBottom: 60 }}>
                        <div>
                            {this.state.orders.length ?
                                this.state.orders.map(v => (
                                    v.goods.length ?
                                        <div key={v.order_id} className="single-order-list">
                                            {v.goods.map(v1 => (
                                                <div key={v1.goods_id} className="single-order">
                                                    <img src={v1.goods_small_logo}
                                                        alt="" />
                                                    <div className="order-content">
                                                        <div className="order-title ellipsis-2">
                                                            {v1.goods_name}
                                                        </div>
                                                        <div className="num-price">
                                                            <span>{v1.goods_price}</span> X <span>{v1.goods_number}</span>
                                                        </div>
                                                        <div className="order-price">
                                                            <span>{v1.goods_number}</span>
                                                            <span></span>
                                                            <span>&yen;{v1.goods_total_price}.00</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            ))}
                                            <div className="order-infos">
                                                <h4 className="title"></h4>
                                                <div className="order-infos-content">
                                                    <div>
                                                        <span></span>
                                                        <span>{v.order_number}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{this.convertTime(v.create_time)}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{v.consignee_addr}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{v.order_fapiao_title}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span>{v.total_count}</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span className="total-price">{v.total_price}.00</span>
                                                    </div>
                                                    <div>
                                                        <span></span>
                                                        <span></span>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>
                                        : ''
                                ))
                                : ''}
                        </div>
                    </WingBlank>

                    <div >

                    </div>
                </Tabs>

                <style jsx>{`
                    :global(.am-tabs) {
                        position: fixed;
                        top: 45px;
                    }
                    `}</style>
            </div>
        )
    }
}

export default OrderList
